import React from "react";
import * as C from '../Header-style';
import AnhorText from './Anhor-text';

const listAnhor = [
   { id: 1, name: 'Mission' },
   { id: 2, name: 'Solution' },
   { id: 3, name: 'Services' },
   { id: 4, name: 'Quote' },
   { id: 5, name: 'Questions' }
]



export default function Anhor({ scrollAnhor }) {

   return (
      <C.AnhorBody>
         <C.AnhorList>
            {listAnhor.map((item) =>
               <li key={item.id}>
                  <AnhorText item={item} scrollAnhor={scrollAnhor} />
               </li>
            )}
         </C.AnhorList>
      </C.AnhorBody >
   )
}