import styled from 'styled-components';


export const TermsWrapper = styled.div`

position: fixed;
z-index: 100;
top: 0;
left: 0;
width: 100vw;
height: 100vh;

background-color: var(--color-modal-background);

overflow-y: auto;
overflow-x: hidden;

@supports (height: 100dvh;) {
     height: 100dvh;
}
`

export const TermsScroll = styled.div`
padding: 0;
  @media (min-width:1100px) {
    padding: 150px 0;
  }
`

export const TermsBody = styled.div`
padding: 30px;
background-color: var(--color-almost-black);

  @media (min-width:768px) {
padding: 70px;
}
  @media (min-width:1100px) {
padding: 100px;
}
`

export const TermsHeader = styled.div`
padding-bottom: 100px;
display: flex;
align-items: flex-start;
justify-content: space-between;
gap:30px
`

export const TermsTitle = styled.div`
position: relative;
z-index: 5;
font-weight: 600;
font-size: 50px;
line-height: 120%;

  @media (min-width:768px) {
font-size: 100px;
}
  @media (min-width:1100px) {
font-size: 140px;
}

`

export const TermsUpdated = styled.div`
padding-bottom: 40px;
font-weight: 500;
font-size: 20px;
line-height: 120%;
letter-spacing: -0.11em;

  @media (min-width:768px) {
font-size: 24px;
}
  @media (min-width:1100px) {
   font-size: 36px;
}
`

export const TermsText = styled.div`
margin-bottom: 100px;
font-size: 20px;
line-height: 120%;
letter-spacing: -0.05em;
color: var(--color-grey-76);

@media (min-width:768px) {
  font-size: 24px;
}
  @media (min-width:1100px) {
    font-size: 32px;
}
`

export const TermsList = styled.ol`
 counter-reset: item;
`
export const TermsListItem = styled.li`
margin-bottom: 80px;
margin-left: 1em;


font-weight: 600;
font-size: 35px;
line-height: 120%;

list-style: decimal;

@media (min-width:768px) {
  font-size: 70px;
}
  @media (min-width:1100px) {
    font-size: 96px;
}

&>*>*{
   font-size: 16px;
   font-weight: 400;
   line-height: 120%;
   letter-spacing: -0.08em;
   color: var(--color-grey-76);

   @media (min-width:768px) {
    font-size: 18px;
   }
     @media (min-width:1100px) {
      font-size: 20px;
   }
}
`
export const TermsListTitle = styled.div`
margin-bottom: 20px;
`

export const TermsListOffset = styled.div`
margin-left: -1em;

&>p{
   margin-bottom: 30px;
  }
`


export const TermsListSubtitle = styled.div`
padding-bottom: 15px;
font-weight: 400;
font-size: 18px;
line-height: 120%;
letter-spacing: -0.08em;
color: var(--color-white-solid);

  @media (min-width:768px) {
font-size: 24px;
}
  @media (min-width:1100px) {
    font-size: 32px;
}

`
export const TermsListSubtext = styled.div`
font-weight: 400;
font-size: 20px;
line-height: 120%;
letter-spacing: -0.08em;
`
export const TermsSubList = styled.ul`
padding-left: 1.7em;
font-weight: 400;
font-size: 20px;
line-height: 120%;
letter-spacing: -0.08em;

li{
list-style: disc;
}
`

export const HideButton = styled.div`
opacity: 0;
pointer-events: none;
`

export const BodyFixedButton = styled.div`
position: sticky;
top: 0;
right: 0;
width: 100%;
`
export const BodyFixedBackground = styled.div`
padding-top: 30px;
padding-bottom: 10px;
background-color: var(--color-almost-black);
position: absolute;
top:0;
right: 0;

width: calc(100% + 10px);

display: flex;
justify-content: flex-end;

@media (min-width:1100px) {
  padding-top: 50px;
}


button{
 
}
`

export const SentButtonClose = styled.div`
display: flex;
justify-content: flex-end;

`





